import { NgModule, NO_ERRORS_SCHEMA, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { AccountHeadRoutingModule } from './account-head-routing.module';
import { AccountHeadComponent } from './account-head.component';
import { AccountHeadAddComponent } from './account-head-add/account-head-add.component';
import { AccountHeadEditComponent } from './account-head-edit/account-head-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    AccountHeadRoutingModule
  ],
  entryComponents:[
    AccountHeadAddComponent,
    AccountHeadEditComponent
  ],
  declarations: [
    AccountHeadComponent,
    AccountHeadAddComponent, 
    AccountHeadEditComponent],
  schemas: [
    NO_ERRORS_SCHEMA,
    CUSTOM_ELEMENTS_SCHEMA
  ]

})
export class AccountHeadModule { }



