import { Component, OnInit, Inject, Output, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { AccountHeadService,ErrorHandleService, } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxCheckBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcheckbox';
import { jqxTreeComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxtree';
import { jqxTreeGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxtreegrid';
// import { TreeviewItem, TreeviewConfig } from 'ngx-treeview';
import { TREE_ACTIONS, KEYS, IActionMapping, ITreeOptions, TreeNode } from 'angular-tree-component';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-account-head-add',
  templateUrl: './account-head-add.component.html',
  styleUrls: ['./account-head-add.component.scss']
})
export class AccountHeadAddComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('myComboBox') myComboBox: jqxComboBoxComponent;
  @ViewChild('myTree') myTree: jqxTreeComponent;
  @ViewChild('subLedgerYes') subLedgerYes: jqxCheckBoxComponent; 
  @ViewChild('subLedgerNo') subLedgerNo: jqxCheckBoxComponent;
  @ViewChild('ledgerYes') ledgerYes: jqxCheckBoxComponent; 
  @ViewChild('ledgerNo') ledgerNo: jqxCheckBoxComponent; 
  @ViewChild('editableYes') editableYes: jqxCheckBoxComponent; 
  @ViewChild('editableNo') editableNo: jqxCheckBoxComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;

  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();
  apiUrl: string;
  ahForm: FormGroup;
  treeSource: any;
  treeDataAdapter: any;
  treeRecords: any = [];
  treeData: any;
  options: ITreeOptions = {
    displayField: 'name',
    idField: 'id',
    hasChildrenField: 'children'
  }


  constructor(
    private fb: FormBuilder,
    private ahService: AccountHeadService,
    private ehs: ErrorHandleService,
    @Inject('API_URL') apiUrl: string, ) {
    this.apiUrl = apiUrl;
    this.createForm();
  }

  ngOnInit() {
    this.initTreeData();
  }
  CheckedYesSubLedger($event){
  this.subLedgerNo.uncheck();
  }
  CheckedNoSubLedger($event){
   this.subLedgerYes.uncheck();   
  }
  CheckedYesLedger($event){
  this.ledgerNo.uncheck();
  
  }
  CheckedNoLedger($event){
  this.ledgerYes.uncheck();
  }
  CheckedYesEditable($event){
  this.editableNo.uncheck();
  }
  CheckedNoEditable($event){
  this.editableYes.uncheck();
  } 
  // NGX-TREEVIEW START
  // items: TreeviewItem[];
  // values: number[];
  // config = TreeviewConfig.create({
  //     hasAllCheckBox: true,
  //     hasFilter: true,
  //     hasCollapseExpand: true,
  //     decoupleChildFromParent: false,
  //     maxHeight: 160
  // });


  // onSelectedChange($event){
  //   console.log($event);
  // }
  // NGX-TREEVIEW ENDS

  ahsource: any;
  ahdataAdapter: any;
  ahcolumns: any;

  initTreeData() {
    let post = [];
    post['limit'] = 'all';

    // for tree grid start
    // this.ahsource =
    // {
    //     dataType: 'json',
    //     dataFields: [
    //         { name: 'id', type: 'number' },
    //         { name: 'parent_id', type: 'number' },
    //         { name: 'code', type: 'string' },
    //         { name: 'name', type: 'string' },
    //     ],
    //     hierarchy:
    //     {
    //         keyDataField: { name: 'id' },
    //         parentDataField: { name: 'parent_id' }
    //     },
    //     id: 'id',
    //     url: this.apiUrl + 'account_head?limit=all'
    // };

    // this.ahdataAdapter = new jqx.dataAdapter(this.ahsource,{
    //   downloadComplete: (data, status, xhr) => {
    //       let tableData = data.data;
    //       console.log(tableData);
    //       return tableData;
    //   }
    // });

    // this.ahcolumns =
    // [
    //     { text: 'Code', dataField: 'code', minWidth: 100, width: 200 },
    //     { text: 'Name', dataField: 'name', minWidth: 100, width: 200 },
    // ];
    // for tree grid end


    this.ahService.index(post).subscribe(
      result => {
        console.log(result);
        this.treeData = result;
        this.treeSource = {
          datatype: 'json',
          datafields: [
            { name: 'id' },
            { name: 'parent_id' },
            { name: 'code' },
            { name: 'name' },
            { name: 'children' }
          ],
          id: 'id',
          localdata: this.treeData
        };
        // create data adapter & perform Data Binding.
        this.treeDataAdapter = new jqx.dataAdapter(this.treeSource, { autoBind: true });
        this.treeRecords = this.treeDataAdapter.getRecordsHierarchy('id', 'parent_id', 'items', [{ name: 'name', map: 'label' }]);
        // for(let i = 0; i < this.treeData.length; i++){
        //   let currentLoop  = this.treeData[i];
        //   if(currentLoop['children'] && currentLoop['children'].length > 0){
        //     console.log('inside loop if ' + currentLoop['id'] + ' ' + currentLoop['name']);
        //     this.myTree.disableItem(currentLoop['id']);
        //   }
        // }
        
      },
      error => {
        console.log(error);
      }
    )
  }

  createForm() {
    this.ahForm = this.fb.group({
      'code': [null, Validators.required],
      'name': [null, Validators.required],
      'mailing_name': [null],
      'mailing_address': [null],
      'mailing_country': [null],
      'mailing_estate': [null],
      'pan_no': [null],      
    });
  }

  saveBtn(post) {
    let selectedData;
    selectedData = this.myTree.getCheckedItems();
    this.subLedgerYes.val() == true ?  post['sub_ledger'] = '1' :  post['sub_ledger'] = '0'; 
    this.ledgerYes.val() == true ?  post['ledger'] = '1' :  post['ledger'] = '0'; 
    this.editableYes.val() == true ?  post['editable'] = '1' :  post['editable'] = '0'; 
    if(selectedData.length > 0){
      post['parent_id'] = selectedData[0].id;
    }else{
      post['parent_id'] = null;
    }
    this.jqxLoader.open();
    this.ahService.store(post).subscribe(
      result => {
        let data = this.ehs.formatError(result);
        if (data['result'] == true) {
          this.AddedData.emit(data);
        } else {
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerHTML = '';
          for (let i = 0; i < data['message'].length; i++) {
            let errorDiv = document.createElement('li');
            errorDiv.innerText = data['message'][i];
            messageDiv.appendChild(errorDiv);
          }
          this.winNotification.open();
        }
        this.jqxLoader.close();
      },
      error => {
        let data = this.ehs.formatError(error);
        console.log(data);
        let messageDiv: any = document.getElementById('windowMessage');
        messageDiv.innerText = data['message'];
        console.log(messageDiv);
        this.winNotification.open();
        this.jqxLoader.close();
      }
    );
   
  }

  TreeCheckChange(e){
    console.log(e);
    if (e.args.checked) {
      let selectedData;
      selectedData = this.myTree.getCheckedItems();
      if(selectedData.length > 1){
        alert('Select Only One');
        this.myTree.uncheckAll();
      }else if(selectedData.length < 1){
        alert('Atleast Select One');
      }else{
  
      }
    }

  }

  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
  [
    { input: '.code', message: 'Code is required !', action: 'keyup, blur', rule: 'required' },
    { input: '.name', message: 'Name is required!', action: 'keyup, blur', rule: 'required' },
  ];
}



